.calculator {
    z-index: 5;
    width: 40rem;
    height: 64rem;
    min-height: 45rem;
    border-radius: .7rem;
    overflow: hidden;
    line-height: 1.1;
    transition: all .5s ease;

    display: flex;
    flex-direction: column;

    @include respond(phone) {
        width: 92%;
        max-height: 90vh;
        height: 70rem;
    }

    &__result {
        overflow-x: scroll;
        overflow-y: hidden;
        flex-basis: 20%;
        width: 100%;
        background-color: $color-dark;
        padding: 3rem;

        display: flex;
        flex-direction: column;
        justify-content: center;
    }

    &__operation {
        white-space: nowrap;
        width: 100%;
        min-height: 1.9rem;
        font-size: 1.9rem;
        font-weight: 300;

        -moz-user-select: none;
        -khtml-user-select: none;
        -webkit-user-select: none;
        -ms-user-select: none;
        user-select: none;

        text-align: end;
        color: $color-secondary;
        margin-top: 1rem;
    }

    &__result-value {
        width: 100%;
        text-align: end;
        color: $color-white;
        font-weight: 300;
        font-size: 6rem;
    }

    &__controls {
        flex: 1;

        display: grid;
        grid-template-rows: repeat(5, 1fr);
        grid-template-columns: repeat(4, 1fr);
    }
}

.negation {
    display: flex;

    &__up {
        transform: translate(-.2rem, -.4rem);
        font-size: 2.4rem;
    }

    &__down {
        transform: translate(.2rem, .4rem);
        font-size: 2.4rem;
    }
}